import { Injectable } from '@angular/core';
import {AngularFirestore,AngularFirestoreCollection,AngularFirestoreDocument} from 'angularfire2/firestore';
import { Item } from '../models/item';
import {Observable} from 'rxjs'
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';




@Injectable({
  providedIn: 'root'
})
export class ItemService {

  itemsCollections:AngularFirestoreCollection<Item>;
  items: Observable<Item[]>;
  itemDoc: AngularFirestoreDocument<Item>;

  constructor(public afs: AngularFirestore) {
   this.items = this.afs.collection('items').valueChanges();

    this.itemsCollections =this.afs.collection('items', ref => ref.orderBy('title','asc'));
    //snapshotchanges for document ids


   this.items =this.itemsCollections.snapshotChanges().map(changes => {
    return changes.map(a => {
      const data =a.payload.doc.data() as Item;
      data.id = a.payload.doc.id;
      return data;
    });
  });

   }
  
   getItems(){
     return this.items;
   }

   addItem(item: Item){
     this.itemsCollections.add(item);
     console.log('submitted service');

   }

   updateItem(item: Item){
    this.itemDoc = this.afs.doc(`items/${item.id}`); 
    this.itemDoc.update(item);
   }


   deleteItem(item: Item){
     this.itemDoc = this.afs.doc(`items/${item.id}`);
     this.itemDoc.delete();
   }

}

